.heading {
  color: #00A0A9;
  font-weight: 700;
  font-size: 32px;
  position: relative;
  display: inline-block;
  margin: 20px 0;
  &::before {
    content: "";
    position: absolute;
    height: 6px;
    width: 50%;
    bottom: -15px;
    left: 0;
    background-color: #835FD9; } }
.main {
  padding-top: 130px;
  min-height: 100vh;
  background-image: url(../images/background.webp);
  background-size: cover;
  background-position: right top;
  background-attachment: fixed; }

.images {}

.clearfix {
  content: "";
  display: table;
  clear: both; }
.container {
  height: 75vh;
  width: 60%;
  padding-bottom: 0;
  overflow: scroll;
  overflow-x: hidden;
  background-color: #fff;
  box-shadow: 10px 0 10px rgba(0, 0, 0, 0.3);
  &::-webkit-scrollbar {
    width: 7px;
    height: 3px;
    background-color: transparent; }
  &::-webkit-scrollbar-button {
    display: none; }
  &::-webkit-scrollbar-track {
    background-color: transparent; }
  &::-webkit-scrollbar-track-piece {
    background-color: transparent; }
  &::-webkit-scrollbar-thumb {
    height: 50px;
    background-color: #835FD9;
    border-radius: 10px; }
  &::-webkit-scrollbar-corner {
    display: none; }
  &::-webkit-resizer {
    display: none; }
  &::before {
    content: "";
    height: 10px;
    width: 100%;
    top: 0;
    left: 0;
    background-color: #fff;
    z-index: 2; } }
.text {
  font-size: 14px;
  line-height: 20px;
  color: #222; }
.textContainer {
  padding-left: 15px; }
.form {
  height: 75vh;
  width: 40%;
  background-image: url(../images/cell.jpg);
  background-size: cover;
  background-position: center; }
.contentWrapper {
  display: flex;
  width: 95vw;
  margin: 0 auto; }
.formContainer {
  width: 100%;
  height: 100%;
  background-color: rgba(0,0,0,0.7);
  padding: 20px 15px;
  form {
    display: flex;
    flex-wrap: wrap; } }

$primary: #00f7e2;
$secondary: #38ef7d;
$white: #fff;
$gray: #b9b9b9;
.form__group {
  position: relative;
  padding: 15px 20px 0 0;
  margin-top: 10px;
  width: 50%; }

.form__groupWrap {
  width: 50%;
  .form__group {
    width: 100%; } }
.form__field {
  font-family: inherit;
  width: 100%;
  border: 0;
  border-bottom: 2px solid $gray;
  outline: 0;
  font-size: 16px;
  color: $white;
  padding: 7px 0;
  background: transparent;
  transition: border-color 0.2s;
  height: 30px;
  &::placeholder {
    color: transparent; }
  &::-webkit-scrollbar {
    width: 7px;
    height: 3px;
    background-color: transparent; }
  &::-webkit-scrollbar-button {
    display: none; }
  &::-webkit-scrollbar-track {
    background-color: transparent; }
  &::-webkit-scrollbar-track-piece {
    background-color: transparent; }
  &::-webkit-scrollbar-thumb {
    height: 50px;
    background-color: #835FD9;
    border-radius: 10px; }
  &::-webkit-scrollbar-corner {
    display: none; }
  &::-webkit-resizer {
    display: none; }

  &:placeholder-shown ~ .form__label {
    font-size: 16px;
    cursor: text;
    top: 20px; }
  &:focus {
    border-bottom: 2px solid #835FD9; } }

.textarea {
  .form__field {
    height: 85px; } }


.form__label {
  position: absolute;
  top: 0;
  display: block;
  transition: 0.2s;
  font-size: 12px;
  color: $gray; }


.form__field:focus {
  ~ .form__label {
    position: absolute;
    top: 0;
    display: block;
    transition: 0.2s;
    font-size: 12px;
    color: $primary;
    font-weight: 700;

    padding-bottom: 6px;
    font-weight: 700;
    border-width: 3px;
    border-image: linear-gradient(to right, $primary,$secondary);
    border-image-slice: 1; } }

/* reset input */
.form__field {
  &:required,&:invalid {
    box-shadow: none; } }

/* demo */
.formTagline {
  font-size: 28px;
  color: #fff;
  font-weight: 700; }

.button {
  background: transparent;
  border: none;
  width: 100%;
  font-size: 18px;
  font-weight: 700;
  color: #ffffff;
  height: 40px;
  margin-top: 10px;
  position: relative;
  z-index: 2;
  &::before {
    content: "";
    position: absolute;
    height: 40px;
    background-color: #835FD9;
    width: 100%;
    top: 50%;
    left: 0;
    transform: translateY(-50%);
    z-index: -1;
    transition: .3s; }
  &:hover {
    cursor: pointer; }
  &:hover::before {
    background-color: #746499; } }
.span {
  color: #fff;
  margin-top: 30px;
  display: block; }
